import { defineComponent } from 'vue';
import { emit } from '@nextcloud/event-bus';
import { generateOcsUrl } from '@nextcloud/router';
import { Permission } from '@nextcloud/files';
import { ShareType } from '@nextcloud/sharing';
import { showError, showSuccess } from '@nextcloud/dialogs';
import { n, t } from '@nextcloud/l10n';
import axios from '@nextcloud/axios';
import NcButton from '@nextcloud/vue/components/NcButton';
import NcDialog from '@nextcloud/vue/components/NcDialog';
import NcLoadingIcon from '@nextcloud/vue/components/NcLoadingIcon';
import NcNoteCard from '@nextcloud/vue/components/NcNoteCard';
import IconCheck from 'vue-material-design-icons/Check.vue';
import IconNext from 'vue-material-design-icons/ArrowRight.vue';
import Config from "../services/ConfigService";
import FileRequestDatePassword from "./NewFileRequestDialog/NewFileRequestDialogDatePassword.vue";
import FileRequestFinish from "./NewFileRequestDialog/NewFileRequestDialogFinish.vue";
import FileRequestIntro from "./NewFileRequestDialog/NewFileRequestDialogIntro.vue";
import logger from "../services/logger";
import Share from "../models/Share.ts";
var STEP;
(function (STEP) {
  STEP[STEP["FIRST"] = 0] = "FIRST";
  STEP[STEP["SECOND"] = 1] = "SECOND";
  STEP[STEP["LAST"] = 2] = "LAST";
})(STEP || (STEP = {}));
const sharingConfig = new Config();
export default defineComponent({
  name: 'NewFileRequestDialog',
  components: {
    FileRequestDatePassword,
    FileRequestFinish,
    FileRequestIntro,
    IconCheck,
    IconNext,
    NcButton,
    NcDialog,
    NcLoadingIcon,
    NcNoteCard
  },
  props: {
    context: {
      type: Object,
      required: true
    },
    content: {
      type: Array,
      required: true
    }
  },
  setup() {
    return {
      STEP,
      n,
      t,
      isShareByMailEnabled: sharingConfig.isMailShareAllowed
    };
  },
  data() {
    return {
      currentStep: STEP.FIRST,
      loading: false,
      destination: this.context.path || '/',
      label: '',
      note: '',
      expirationDate: null,
      password: null,
      share: null,
      emails: []
    };
  },
  computed: {
    finishButtonLabel() {
      if (this.emails.length === 0) {
        return t('files_sharing', 'Close');
      }
      return n('files_sharing', 'Send email and close', 'Send {count} emails and close', this.emails.length, {
        count: this.emails.length
      });
    }
  },
  methods: {
    onPageNext() {
      const form = this.$refs.form;
      // Reset custom validity
      form.querySelectorAll('input').forEach(input => input.setCustomValidity(''));
      // custom destination validation
      // cannot share root
      if (this.destination === '/' || this.destination === '') {
        const destinationInput = form.querySelector('input[name="destination"]');
        destinationInput?.setCustomValidity(t('files_sharing', 'Please select a folder, you cannot share the root directory.'));
        form.reportValidity();
        return;
      }
      // If the form is not valid, show the error message
      if (!form.checkValidity()) {
        form.reportValidity();
        return;
      }
      if (this.currentStep === STEP.FIRST) {
        this.currentStep = STEP.SECOND;
        return;
      }
      this.createShare();
    },
    onRemoveEmail(email) {
      const index = this.emails.indexOf(email);
      this.emails.splice(index, 1);
    },
    onCancel() {
      this.$emit('close');
    },
    async onFinish() {
      if (this.emails.length === 0 || this.isShareByMailEnabled === false) {
        showSuccess(t('files_sharing', 'File request created'));
        this.$emit('close');
        return;
      }
      if (sharingConfig.isMailShareAllowed && this.emails.length > 0) {
        await this.setShareEmails();
        await this.sendEmails();
        showSuccess(n('files_sharing', 'File request created and email sent', 'File request created and {count} emails sent', this.emails.length, {
          count: this.emails.length
        }));
      } else {
        showSuccess(t('files_sharing', 'File request created'));
      }
      this.$emit('close');
    },
    async createShare() {
      this.loading = true;
      let expireDate = '';
      if (this.expirationDate) {
        const year = this.expirationDate.getFullYear();
        const month = (this.expirationDate.getMonth() + 1).toString().padStart(2, '0');
        const day = this.expirationDate.getDate().toString().padStart(2, '0');
        // Format must be YYYY-MM-DD
        expireDate = `${year}-${month}-${day}`;
      }
      const shareUrl = generateOcsUrl('apps/files_sharing/api/v1/shares');
      try {
        const request = await axios.post(shareUrl, {
          // Always create a file request, but without mail share
          // permissions, only a share link will be created.
          shareType: sharingConfig.isMailShareAllowed ? ShareType.Email : ShareType.Link,
          permissions: Permission.CREATE,
          label: this.label,
          path: this.destination,
          note: this.note,
          password: this.password || '',
          expireDate: expireDate || '',
          // Empty string
          shareWith: '',
          attributes: JSON.stringify([{
            value: true,
            key: 'enabled',
            scope: 'fileRequest'
          }])
        });
        // If not an ocs request
        if (!request?.data?.ocs) {
          throw request;
        }
        const share = new Share(request.data.ocs.data);
        this.share = share;
        logger.info('New file request created', {
          share
        });
        emit('files_sharing:share:created', {
          share
        });
        // Move to the last page
        this.currentStep = STEP.LAST;
      } catch (error) {
        const errorMessage = error?.response?.data?.ocs?.meta?.message;
        showError(errorMessage ? t('files_sharing', 'Error creating the share: {errorMessage}', {
          errorMessage
        }) : t('files_sharing', 'Error creating the share'));
        logger.error('Error while creating share', {
          error,
          errorMessage
        });
        throw error;
      } finally {
        this.loading = false;
      }
    },
    async setShareEmails() {
      this.loading = true;
      // This should never happen™
      if (!this.share || !this.share?.id) {
        throw new Error('Share ID is missing');
      }
      const shareUrl = generateOcsUrl('apps/files_sharing/api/v1/shares/{id}', {
        id: this.share.id
      });
      try {
        // Convert link share to email share
        const request = await axios.put(shareUrl, {
          attributes: JSON.stringify([{
            value: this.emails,
            key: 'emails',
            scope: 'shareWith'
          }, {
            value: true,
            key: 'enabled',
            scope: 'fileRequest'
          }])
        });
        // If not an ocs request
        if (!request?.data?.ocs) {
          throw request;
        }
      } catch (error) {
        this.onEmailSendError(error);
        throw error;
      } finally {
        this.loading = false;
      }
    },
    async sendEmails() {
      this.loading = true;
      // This should never happen™
      if (!this.share || !this.share?.id) {
        throw new Error('Share ID is missing');
      }
      const shareUrl = generateOcsUrl('apps/files_sharing/api/v1/shares/{id}/send-email', {
        id: this.share.id
      });
      try {
        // Convert link share to email share
        const request = await axios.post(shareUrl, {
          password: this.password || undefined
        });
        // If not an ocs request
        if (!request?.data?.ocs) {
          throw request;
        }
      } catch (error) {
        this.onEmailSendError(error);
        throw error;
      } finally {
        this.loading = false;
      }
    },
    onEmailSendError(error) {
      const errorMessage = error.response?.data?.ocs?.meta?.message;
      showError(errorMessage ? t('files_sharing', 'Error sending emails: {errorMessage}', {
        errorMessage
      }) : t('files_sharing', 'Error sending emails'));
      logger.error('Error while sending emails', {
        error,
        errorMessage
      });
    }
  }
});