import { defineComponent } from 'vue';
import { getFilePickerBuilder } from '@nextcloud/dialogs';
import { t } from '@nextcloud/l10n';
import IconFolder from 'vue-material-design-icons/Folder.vue';
import IconInfo from 'vue-material-design-icons/Information.vue';
import IconLock from 'vue-material-design-icons/Lock.vue';
import NcTextArea from '@nextcloud/vue/components/NcTextArea';
import NcTextField from '@nextcloud/vue/components/NcTextField';
export default defineComponent({
  name: 'NewFileRequestDialogIntro',
  components: {
    IconFolder,
    IconInfo,
    IconLock,
    NcTextArea,
    NcTextField
  },
  props: {
    disabled: {
      type: Boolean,
      required: false,
      default: false
    },
    context: {
      type: Object,
      required: true
    },
    label: {
      type: String,
      required: true
    },
    destination: {
      type: String,
      required: true
    },
    note: {
      type: String,
      required: true
    }
  },
  emits: ['update:destination', 'update:label', 'update:note'],
  setup() {
    return {
      t
    };
  },
  methods: {
    onPickDestination() {
      const filepicker = getFilePickerBuilder(t('files_sharing', 'Select a destination')).addMimeTypeFilter('httpd/unix-directory').allowDirectories(true).addButton({
        label: t('files_sharing', 'Select'),
        callback: this.onPickedDestination
      }).setFilter(node => node.path !== '/').startAt(this.destination).build();
      try {
        filepicker.pick();
      } catch (e) {
        // ignore cancel
      }
    },
    onPickedDestination(nodes) {
      const node = nodes[0];
      if (node) {
        this.$emit('update:destination', node.path);
      }
    }
  }
});